import React, { Component } from 'react'
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import { isAuthenticated, getRole } from "./services/auth";

import SignUp from "./pages/SignUp";
import SignIn from "./pages/SignIn";
import ChatUser from "./pages/Chat/User/ChatLoginUser";
import ChatAdmin from "./pages/Chat/Admin/ChatLoginAdmin";
import Admin from "./pages/Chat/Admin";


export class ChatLoginUser extends Component {
  constructor(props) {
    super(props)
    this.state = {
      role: null
    }
  }

  async componentDidMount() {
    const r = await getRole()
    this.setState({ role: r })
  }
}

const PrivateRoute = ({ component: Component, ...rest }) => (

  <Route
    {...rest}
    render={props =>
      isAuthenticated() ? (
        <Component {...props} />
      ) : (
          <Redirect to={{ pathname: "/", state: { from: props.location } }} />
        )
    }
  />
);

const AdminRoute = ({ component: Component, chat: Chat, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      (isAuthenticated() /*&& Chat.props.data.role === "ADMIN"*/) ? (
        <Component {...props} />
      ) : (
          <Redirect to={{ pathname: "/", state: { from: props.location } }} />
        )
    }
  />
);

const Routes = () => (
  <BrowserRouter>
    <Switch>
      <Route exact path="/" component={SignIn} />
      <PrivateRoute path="/chat" component={ChatUser} />

      <AdminRoute path="/admin" component={Admin} chat={ChatLoginUser} />
      <Route path="/signup" component={SignUp} />
      <AdminRoute path="/chatAdmin" component={ChatAdmin} />

      <Route path="*" >
        <Redirect to={{ pathname: "/" }} />
      </Route>
    </Switch>
  </BrowserRouter>
);

export default Routes;



